body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.board {
  height: 300px;
  width: 300px;
  display: flex;
  flex-direction: column;
  background-color: teal;
  margin: auto;
}

div.row {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

div.cell {
  height: 100%;
  width: 100%;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  cursor: pointer;
  display: flex;
}

div.cell:hover {
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.75);
}

div.cell.white {
}

div.cell.white>div {
  background-color: rgba(255, 255, 255, 0.5);
  width: 80%;
  height: 80%;
  border-radius: 50%;
  margin: auto;
  transition: background-color 0.5s ease;
}

div.cell.black {
}

div.cell.black>div {
  background-color: rgba(0, 0, 0, 0.5);
  width: 80%;
  height: 80%;
  border-radius: 50%;
  margin: auto;
  transition: background-color 0.5s ease;
}

div.status {
  width: 300px;
  margin: auto;
  display: flex;
  margin-top: 20px;
  background: teal;
}

div.status > div {
  width: 100%;
  border: 5px outset rgb(0, 100, 100);
  padding: 5px;
  display: flex;
}


/* div.status > div.active {
  border-color: rgb(0, 155, 155);
} */

div.status .status-black {
  text-align: right;
}

div.circle {
  height: 100%;
  width: 15%;
  border-radius: 50%;
  margin: auto;
}

div.circle.white {
  background-color: rgba(255, 255, 255, 0.5);
}

div.circle.black {
  background-color: rgba(0, 0, 0, 0.5);
}

div.arrow {
  visibility: hidden;
  margin: auto;
}

div.active > div.arrow {
  visibility: visible;
}

div.message-box {
  position: absolute;
  margin: auto;
  z-index: 999;
  display: none;
  left: 50%;
  width: 200px;
  margin-left: -100px;
  top: 150px;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: aliceblue;
  border: 1px solid black;

}

div.message-box.visible {
  display: block;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

